import { Surfaces } from "@netlify/sdk--ui-core";
import React from "react";
import { useNetlifySDK } from "../../use_netlify_sdk.js";

export type VisualEditorConfigurationSurfaceProps = {
  /**
   * Content to render within the element.
   */
  children?: React.ReactNode | undefined;

  /**
   * A class property to attach to the element.
   *
   * @see {@link !Element.className}
   */
  className?: string | undefined;
};

/**
 * A container element used to render the Visual Editor Configuration surface. This component will
 * take care of styling your component so it looks good under **Site configuration > Visual editor > Content sources > Add a content source**
 * in the Netlify UI.
 *
 * <!-- TODO(ndhoule): Add a screenshot here -->
 *
 * @param props
 * @see Surfaces.VisualEditorConfiguration
 * @example
 * ```tsx
 * import { VisualEditorConfigurationSurface } from "@netlify/sdk/react/components";
 *
 * <VisualEditorConfigurationSurface>
 *   <div>Your surface content goes here</div>
 * </VisualEditorConfigurationSurface>
 * ```
 */
export const VisualEditorConfigurationSurface = ({
  className,
  children,
  ...otherProps
}: VisualEditorConfigurationSurfaceProps) => {
  const sdk = useNetlifySDK();

  return (
    <section
      id={`section-${Surfaces.VisualEditorConfiguration}-${sdk.extension.slug}`}
      className={className}
      {...otherProps}
    >
      {children}
    </section>
  );
};
